y alternantes. El ritmo vital está íntimamente ligado a la producción del lenguaje, el aparato fonador tiene una doble función: garantizar el oxígeno y la comunicación. Además del ritmo natural, hay un ritmo planificado en el lenguaje, creado por el hombre y, por ende, artificial. El ritmo está constituido básicamente por la repetición de un patrón y es esencial para la percepción de los acontecimientos que ocurren en el tiempo. Se da entonces, como vimos, en la vida humana, pero también en la música y en el lenguaje.")])])]),_c('div',{staticClass:"col-8 col-lg-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema2/img10.png")}})])])]),_c('div',{staticClass:"row justify-content-center my-lg-4"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-lg-0 pt-4 pt-0 position-relative"},[_c('div',{staticClass:"number",attrs:{"data-number":"04"}},[_c('h4',{staticClass:"ps-4"},[_vm._v("Inflexión")]),_c('p',{staticClass:"ps-4 mb-0"},[_vm._v("El diccionario de la Real Academia (1997), citado por Ortego et al. (s. f., p. 17), define la inflexión como: “la elevación o atenuación que se hace con la voz quebrándola o pasando de un tono a otro”. Sirve para comunicar sentimientos y emociones. Poca entonación, con un volumen bajo, indica aburrimiento o tristeza. Un patrón que no varía puede ser aburrido o monótono. Se percibe a la gente como más dinámica y extrovertida cuando cambian el tono y la inflexión de sus voces a menudo durante una conversación.")])])]),_c('div',{staticClass:"col-8 col-lg-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema2/img11.png")}})])])])])],1),_c('div',{staticClass:"bg_grad-05"},[_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("El guía de turismo debe acostumbrar a impostar su voz, se trata de usar, adecuadamente, los órganos de la voz, proyectando el sonido de forma agradable, sin denotar duda o temblor evidenciando miedo al público, que sea poderosa y agradable al mismo tiempo y que hasta la última fila del 